@use 'sass:math';
@import '../../styles/settings.scss';

.product {
  display: flex;
  flex-wrap: wrap;
  margin: ($base-size * 6) ($base-size * 3);
  background: $background-gradient;
  border-radius: $radius;
  box-shadow: $box-shadow;
  padding: ($base-size * 2) $base-size;
  position: relative;
}

.imageContainer {
  flex: 0 0 40%;
  width: 40%;
  padding: 0 ($base-size * 2);
  margin-left: -($base-size * 4);
  margin-top: -($base-size * 3);
  min-width: 200px;
}


.name {
  margin: $base-size 0;
  font-size: $base-size * 3;
}

.price {
  font-size: $base-size * 1.5;
}
